.el-zoom-in-top-enter-active,
.el-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1) 100ms, opacity 300ms cubic-bezier(0.23, 1, 0.32, 1) 100ms;
  transform-origin: center top;
}
.el-zoom-in-top-enter,
.el-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0);
}
.el-zoom-in-center-enter-active,
.el-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1);
}
.el-zoom-in-center-enter,
.el-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0);
}
.region-picker {
  font-size: 14px;
  position: relative;
  display: inline-block;
  width: 100%;
  user-select: none;
}
.region-picker .picker-toggle {
  position: relative;
  cursor: pointer;
}
.region-picker .picker-toggle input.placeholder {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border: none;
}
.region-picker .picker-toggle.disabled {
  cursor: not-allowed;
}
.region-picker .picker-toggle.disabled input {
  background-color: #eef1f6;
  border-color: #d1dbe5;
  color: #bbb;
}
.region-picker .picker-toggle.disabled .picker-label {
  color: #bbb;
}
.region-picker .picker-toggle.disabled .picker-input-icon {
  cursor: not-allowed;
}
.region-picker .picker-toggle .picker-label {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  line-height: 36px;
  padding: 0 25px 0 10px;
  color: #1f2d3d;
  width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  box-sizing: border-box;
  font-size: 14px;
  text-align: left;
}
.region-picker .picker-toggle input {
  background-color: #fff;
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
  border-radius: 4px;
  border: 1px solid #c0ccda;
  color: #1f2d3d;
  font-size: inherit;
  height: 36px;
  box-sizing: border-box;
  line-height: 1;
  padding: 3px 15px 3px 10px;
  width: 100%;
}
.region-picker .picker-toggle input:hover {
  border-color: #8391a5;
}
.region-picker .picker-toggle input:focus {
  outline: none;
  border-color: #20a0ff;
}
.region-picker .picker-toggle .picker-input-icon {
  position: absolute;
  top: 11px;
  right: 10px;
