  cursor: pointer;
}
.region-picker .picker-toggle .picker-input-icon.el-icon-circle-close:hover {
  color: #8391a5;
}
.region-picker .picker-toggle.opened.picker-input-icon {
  border-bottom-color: #e0e6ed;
}
.region-picker .picker-menu {
  position: absolute;
  max-height: 400px;
