.region-picker-multiple .picker-toggle.disabled .selected-labels {
  cursor: not-allowed;
  background-color: #eef1f6;
  border-color: #d1dbe5;
  color: #bbb;
}
.region-picker-multiple .picker-toggle.disabled .selected-labels .selected-label i:hover {
  color: #20a0ff;
  background: transparent;
}
.region-picker-multiple .selected-labels {
  user-select: none;
  min-width: 50px;
  background: #fff;
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
  border-radius: 4px;
  border: 1px solid #c0ccda;
  color: #1f2d3d;
  font-size: inherit;
  min-height: 36px;
  box-sizing: border-box;
  padding: 3px 15px 3px 10px;
  width: 100%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.region-picker-multiple .selected-labels:hover {
  border-color: #8391a5;
}
.region-picker-multiple .selected-labels:focus {
  outline: none;
  border-color: #20a0ff;
}
.region-picker-multiple .selected-labels .selected-label {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin: 3px 3px;
  background-color: #8391a5;
  padding: 5px;
  height: 24px;
  line-height: 24px;
  font-size: 12px;
  color: #fff;
  border-radius: 4px;
  box-sizing: border-box;
  border: 1px solid transparent;
  background-color: rgba(32,160,255,0.1);
  border-color: rgba(32,160,255,0.2);
  color: #20a0ff;
}
.region-picker-multiple .selected-labels .selected-label i {
  margin-left: 3px;
  font-size: 10px;
  padding: 2px;
  border-radius: 50%;
}
.region-picker-multiple .selected-labels .selected-label i:hover {
  background-color: #20a0ff;
  color: #fff;
}
.region-picker-multiple .selected-labels .selected-label:first-child {
  margin-left: 0;
}
.region-picker-multiple .picker-search {
  padding: 10px;
}
.region-picker-multiple .picker-search input {
  background: #fff;
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis;
  border-radius: 4px;
  border: 1px solid #c0ccda;
  color: #1f2d3d;
  font-size: inherit;
  height: 36px;
  box-sizing: border-box;
  line-height: 1;
  padding: 3px 15px 3px 10px;
  width: 100%;
}
.region-picker-multiple .picker-search input:hover {
  border-color: #8391a5;
}
.region-picker-multiple .picker-search input:focus {
  outline: none;
  border-color: #20a0ff;
}
.region-picker-multiple .scroll-options,
.region-picker-multiple .scroll-search {
  border-top: 1px solid #e0e6ed;
  border-bottom: 1px solid #e0e6ed;
}
.region-picker-multiple .picker-opertaions {
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.region-picker-multiple .picker-opertaions button {
  margin: 0 5px;
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 1px solid #bfcbd9;
  color: #1f2d3d;
  border-radius: 4px;
  padding: 7px 9px;
  font-size: 12px;
  border-radius: 4px;
  outline: 0;
  text-align: center;
}
.region-picker-multiple .picker-opertaions button:hover {
  color: #20a0ff;
  border-color: #20a0ff;
}
.region-picker-multiple .picker-opertaions button.primary {
  color: #fff;
  background-color: #20a0ff;
  border-color: #20a0ff;
}
.region-picker-multiple .picker-opertaions button.primary:hover {
  background: #4db3ff;
  border-color: #4db3ff;
  color: #fff;
}
.region-picker-multiple .scroll-option li.place-checkbox {
  padding-left: 25px;
}
.region-picker-multiple .place-checkbox {
  padding-left: 25px;
  background-position: left;
  background-position-x: 5px;
  background-repeat: no-repeat;
  cursor: pointer;
}
.region-picker-multiple .place-checkbox.on {
  background-image: url("./images/checkbox_on.svg");
}
.region-picker-multiple .place-checkbox.off {
  background-image: url("./images/checkbox_off.svg");
}
.region-picker-multiple .place-checkbox.part {
  background-image: url("./images/checkbox_part.svg");
}
